* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
*:before,
*:after {
  box-sizing: border-box;
}
.container:before,
.container:after {
  display: table;
  content: " ";
  clear: both;
}
.container {
  width: 960px;
  margin: 0 auto;
  clear: both;
}
h1,
h2,
h3,
h4,
h5,
h6 {
  color: #333;
}
p {
  font-size: 12px;
  margin-bottom: 5px;
  color: #333;
}
a {
  text-decoration: none;
}
#footer {
  margin-top: 15px;
}
#blogs .blog {
  margin-top: 15px;
  border-bottom: 1px #ccc solid;
  padding-bottom: 5px;
}
#blogs .blog .title {
  font-size: 16px;
  font-weight: bold;
  margin-bottom: 15px;
}
#blogs .blog .title a {
  color: #555;
}
#blogs .blog .title a:hover {
  color: #333;
}
#blogs .blog .time {
  color: #aaa;
}
#blogs .blog .time {
  color: #777;
}
#paginate {
  margin-top: 30px;
}
#paginate a {
  font-size: 14px;
  text-decoration: none;
}
#paginate .prev {
  float: left;
}
#paginate .next {
  float: right;
}
